/* eslint-disable no-shadow */

import {
  getValueByPath,
} from '@/assets/js/utils';

const state = {
  list: [{
    name: '杭电',
    startTime: '2013-09',
    endTime: '2017-07',
    info: '软件工程,本科',
    detail: '',
    logo: '/resume-card/static/image/hd.jpg',
  }]
};

const getters = {
  firstEducation(state) {
    return getValueByPath(state.list, '[0]', {});
  }
};

export default {
  namespaced: true,
  state,
  getters,
};



